/* Center the entire app content */
.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: #f0f0f5;
  font-family: Arial, sans-serif;
}

/* General button styling */
button {
  margin: 10px;
  padding: 12px 24px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #45a049;
}

/* Special styling for the landing page buttons */
.landing-button {
  width: 200px;
  font-size: 18px;
  margin-top: 20px;
}

/* Centering and styling for form containers */
form {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: white;
  padding: 30px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  width: 100%;
  max-width: 400px;
  margin-top: 20px;
}

/* Input fields and dropdown styling */
input, select {
  margin: 10px;
  padding: 12px;
  font-size: 16px;
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

/* Input focus effect */
input:focus, select:focus {
  border-color: #4CAF50;
  outline: none;
}

/* Error message styling */
p {
  color: red;
  font-weight: bold;
  margin-top: 10px;
}

/* Header styling */
h1 {
  font-size: 28px;
  color: #333;
  margin-bottom: 20px;
}

h2 {
  font-size: 24px;
  color: #333;
  margin-bottom: 15px;
}

/* Responsive form adjustments */
@media (max-width: 500px) {
  .App {
    padding: 20px;
  }
  
  form {
    padding: 20px;
  }

  button, input, select {
    font-size: 14px;
    padding: 10px;
  }

  h1 {
    font-size: 24px;
  }

  h2 {
    font-size: 20px;
  }
}
